.button {
  min-width: 160px;
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 0;
  border-radius: 22px;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 35px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.button--primary {
  color: #fff;
  fill: #fff;
  background-color: #d41443;
}

.button--primary:hover, .button--primary:focus {
  background-color: #c7133e;
}

.button--secondary {
  color: #d41443;
  fill: #d41443;
  background-color: #fff;
}

.button--secondary:hover, .button--secondary:focus {
  background-color: #ecebeb;
}

.button__icon {
  width: 8px;
  height: 8px;
  margin-left: 14px;
}

.button:hover .button__icon, .button:focus .button__icon {
  transform: scale(1.7);
}

.more {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.more:hover, .more:focus {
  background-color: #ecebeb;
  transform: scale(1.2);
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

